<template>
    <div>
        <Row>
            <Card>
                <div slot="title">
                    <Row>
                        <Col span="6" style="padding-left: 10px; padding-right: 10px;">
                            <Input v-model="token" size="large" placeholder="token"></Input>
                        </Col>
                        <Col span="6" style="padding-left: 10px; padding-right: 10px;">
                            <Input v-model="communityId" size="large" placeholder="支付宝小区编号"></Input>
                        </Col>
                        <Col span="6" style="padding-left: 10px; padding-right: 10px;">
                            <Input v-model="roomId" size="large" placeholder="房间id"></Input>
                        </Col>
                        <Col span="6" style="padding-left: 10px; padding-right: 10px;">
                            <Button type="primary" icon="ios-search" @click="getBillList">查询</Button>
                        </Col>
                    </Row>

                </div>
                <Row>
                    <Table stripe border :columns="billColumns" :data="billData"></Table>
                    <Page style="margin: 10px 0;" :total="pageTotal" :current="pageNum" :page-size="pageSize" show-total show-sizer show-elevator
                          @on-change="handlePage" @on-page-size-change='handlePageSize'></Page>
                </Row>
            </Card>
        </Row>
    </div>
</template>

<script>
    export default {
        name: 'directive_page',
        data () {
            return {
                token: '201709BBdb06c71a5bd0432193e9a992ac3f7X20',
                communityId: 'A6IWVHER03301',
                roomId: '41844',
                pageTotal: 0,
                pageNum: 1,
                pageSize: 10,
                billColumns: [
                    {
                        title: '房间id',
                        key: 'out_room_id'
                    },{
                        title: '账单id',
                        key: 'bill_entry_id'
                    },
                    {
                        title: '金额',
                        key: 'bill_entry_amount'
                    },
                    {
                        title: '房间号',
                        key: 'room_address'
                    },
                    {
                        title: '状态',
                        key: 'status',
                        render: function (h, params) {
                            let texts = '';
                            if (params.row.status === 'WAIT_PAYMENT') {
                                texts = "待缴且未过缴费截止日期"
                            }else if(params.row.status === 'FINISH_PAYMENT'){
                                texts = "用户完成支付和销账"
                            }else if(params.row.status === 'UNDER_PAYMENT'){
                                texts = "账单锁定待用户完成支付"
                            }else if(params.row.status === 'OUT_OF_DATE'){
                                texts = "未支付且已过缴费截止日期"
                            }
                            return h('div', {
                                props: {
                                },
                            },texts)
                        }
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'warning',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index, params.row.bill_entry_id);
                                        }
                                    }
                                }, 'Delete')
                            ]);
                        }
                    }
                ],
                billData: []
            };
        },
        created: function () {
            this.getBillList();
        },
        methods: {
            getBillList () {
                this.$axios.get('/alipay/queryAlipayBill', {
                    params: {
                        token: this.token,
                        communityId: this.communityId,
                        roomId: this.roomId,
                        pageNum: this.pageNum,
                        pageSize:  this.pageSize
                    }
                }).then(res => {
                    if (res.data.code == 10000) {
                        this.billData = res.data.bill_result_set;
                        this.pageTotal = res.data.total_bill_count
                    }else {
                        this.billData = []
                        this.$Modal.error({
                            title: '消息提示',
                            content: res.data.sub_msg
                        });
                    }

                    //console.log(res.data);
                }).catch(error => {
                    console.log(error);
                });
            },
            remove (index, billId) {

                this.$axios.get('/alipay/deleteAlipayBill', {
                    params: {
                        token: this.token,
                        communityId: this.communityId,
                        billId: billId
                    }
                }).then(res => {
                    if (res.data.code == 10000) {
                        this.billData.splice(index, 1);
                    }else {
                        this.billData = []
                        this.$Modal.error({
                            title: '消息提示',
                            content: res.data.sub_msg
                        });
                    }
                    //console.log(res.data);
                }).catch(error => {
                    console.log(error);
                });
            },
            handlePage(value) {
                this.pageNum = value
                this.getBillList()
            },
            handlePageSize(value) {
                this.pageSize = value
                this.getBillList()
            }
        }
    };
</script>

